body {
    background-color: #2d3436; 
    margin: 20px;
}

.jumbotron {
    background-color: #d63031;
}

.video-item img {
    max-width: 64px;
}

.video-detail .details {
    margin-top: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 2px;
    background-color: #55efc4
}

.list-group-item {
    cursor: pointer;
}

.list-group-item:hover {
    background-color: #eee;
}


